@font-face {
  font-family: "Roboto";
  src: url("../../fonts/RobotoLight.woff2") format("woff2"), url("../../fonts/RobotoLight.woff") format("woff");
  font-style: normal;
  font-weight: 300;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../../fonts/RobotoBold.woff2") format("woff2"), url("../../fonts/RobotoBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "rubikmonoone";
  src: url("../../fonts/rubikmonooneregular.woff2") format("woff2"), url("../../fonts/rubikmonooneregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 300;
  color: #000000;
  background-color: #ffffff;
  position: relative; }

img {
  max-width: 100%;
  height: auto; }

*,
*::before,
*::after {
  box-sizing: border-box; }

@media (max-width: 320px) {
  .container {
    max-width: 298px; } }

@media (min-width: 744px) {
  .container {
    max-width: 724px; } }

@media (min-width: 1170px) {
  .container {
    max-width: 1170px; } }

.logo__link {
  display: block; }

.logo__img {
  display: block;
  width: 120px;
  height: 30px; }
